.embed-calc-header {
  width: 100%;
  text-align: left;
  padding: 10px;
  color: #333;
  background: #f4f9ff;
  margin: 0; }

.embed-calc-bottom {
  background: #2b5a99;
  margin: 0;
  color: #fff;
  padding: 10px;
  font-weight: bold; }

.allcompanies-front {
  position: relative;
  font-size: 28px;
  color: #fff;
  line-height: 1.4;
  display: block;
  max-width: 640px;
  margin: 0 auto;
  text-align: center;
  margin: 0 auto 20px auto; }

.allcompanies-front::before {
  content: '';
  position: absolute;
  height: 100%;
  transform: translateY(0) rotate(-0.3deg);
  width: 100%;
  background: #1b1b1b;
  left: -1%;
  z-index: -1;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4); }

.disclosure {
  border-left: 2px solid #FFC40E; }

.strong-rub {
  border-bottom: 1px solid #FFC40E; }

.iframe-calc-front {
  padding: 0;
  margin: 0; }
  .iframe-calc-front iframe {
    max-width: 100%; }

@media all and (max-width: 520px) {
  .front-header__container h3 {
    font-size: 2.5rem; } }

@media all and (max-width: 640px) {
  .below-main-content h2 {
    font-size: 1.8rem; }
  .front-header__container {
    width: 100%; }
  .newcomps__header__number {
    grid-template-columns: none;
    grid-template-rows: 1fr 1fr;
    max-height: 80px; }
    .newcomps__header__number h1 {
      font-size: 2.5rem; }
  .below-main-content {
    padding: 20px 10px; }
    .below-main-content ul {
      padding-left: 0; } }

@media all and (max-width: 1010px) {
  .iframe-calc-front {
    padding: 0 2rem 1rem; }
  .embed-calc-header {
    background: #2b5a99;
    color: #fff; }
  .iframe-calc-front {
    padding-top: 10px; }
  .newcompanies {
    font-size: 2rem; }
  .embed-calc-header {
    font-size: 2rem; } }

main {
  background-color: #fff;
  border: 1px solid #ececec;
  box-shadow: 0 1px #ececec;
  padding: 40px;
  margin: 0 0 40px; }
  main h1 {
    font-size: 4rem; }
  main h2 {
    width: 100%;
    text-align: left;
    padding: 10px 0px;
    color: #fff;
    background: #333;
    border-left: 50px solid #ffc600;
    font-weight: normal; }
    main h2 strong {
      margin-left: 10px;
      font-weight: normal;
      display: inline-block; }
  main article .entry-meta {
    font-size: 15px;
    margin-bottom: 0;
    color: #000;
    border-bottom: solid 2px #000;
    border-top: solid 2px #000;
    padding: 10px 0;
    line-height: 1;
    font-weight: bold; }
  main article ol li {
    line-height: 2; }
  main article ul li {
    list-style: none;
    line-height: 2; }
  main article ul li::before {
    content: '■';
    margin-right: 0.5rem;
    color: #ffc600;
    font-size: 20px; }

@media all and (max-width: 600px) {
  main {
    padding: 10px; } }

.sidebar {
  background: #212326;
  height: 540px;
  color: #fff;
  font-size: 1.8rem; }
  .sidebar__currency {
    margin-bottom: 10px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    background: #2c3035; }
    .sidebar__currency__name--title {
      color: #FFC40E; }
    .sidebar__currency__rate {
      display: flex;
      justify-content: space-between;
      width: 200px; }
  .sidebar__posts__container .sidebar__post {
    color: #fff;
    display: grid;
    grid-template-columns: 100px 1fr;
    justify-items: center;
    align-items: center;
    padding: 10px;
    border-bottom: none; }
    .sidebar__posts__container .sidebar__post--img {
      width: 100px;
      height: 100px; }
      .sidebar__posts__container .sidebar__post--img img {
        width: 100%;
        height: 100%;
        object-fit: cover; }
    .sidebar__posts__container .sidebar__post h4 {
      font-size: 1.8rem;
      letter-spacing: 0.5px;
      font-weight: normal; }

.top:after {
  content: '';
  display: block;
  background: #F9BA00;
  height: 10px;
  width: 100%; }



