.front-header {
  background: #fff;
  padding: 2rem 2rem 2rem;
    &__container {
      width: 70%;
      margin: 0 auto;
      text-align: center;
      h3 {
        font-weight: bold;
        font-size: 3rem;
      }
    }

  p {
    line-height: 1.4;
    margin: 1rem 0;
  }
  ul {
    margin: 10px 0;
    line-height: 1.4;
    padding-left: 2rem;
  }
  ul li {
    list-style: none;
  }
  ul li:before {
    content: '\25A0';
    margin-right: 0.5rem;
    color: #ffc600;
    font-size: 20px;
  }
}

.front-grid {
  max-width: 720px;
}

.zero-padding-margin {
  padding: 0;
  margin: 0;
}

.newcomps {
  background: #fff;
  padding: 10px;
  margin: 2rem 0;
  display: grid;
  grid-template-rows: 1fr 1fr;
  justify-content: center;
  justify-items: center;
  align-items: center;
  &__header__number {
    display: grid;
    grid-template-columns: 2fr 1fr;
    justify-items: center;
    align-items: center;
    h1 {
      font-weight: normal;
      font-size: 3rem;
      margin: 0;
    }
    &--num {
      background: #ECF0F1;
      clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
      padding: 10px 50px;
    }
  }
  &__bullets {
    ul {
      margin-top: 0;
    }
    ul li {
      list-style: none;
    }
    ul li:before {
      content: '\25A0';
      margin-right: 0.5rem;
      color: #ffc600;
      font-size: 20px;
    }
  }
}

.below-main-content {
  background: #fff;
  padding: 20px;
  margin: 2rem 0;
  h2 {
    letter-spacing: 0.5px;
    font-weight: normal;
    font-size: 2rem;
    margin: 1rem auto 2rem;
    width: 100%;
    text-align: left;
    padding: 10px 0px;
    color: #fff;
    background: #333;
    border-left: 50px solid #ffc600;
  }
  span {
    margin-left: 10px;
    display: inline-block;
  }
  p {
    line-height: 1.4;
  }
  ul {
    margin: 10px 0;
    line-height: 1.4;
    padding-left: 2rem;
  }

  ul li {
    list-style: none;
  }

  ul li:before {
    content: '\25A0';
    margin-right: 0.5rem;
    color: #ffc600;
    font-size: 20px;
  }

}

