.embed-calc-header {
  width: 100%;
  text-align: left;
  padding: 10px;
  color: #333;
  background: #f4f9ff;
  margin: 0;
}

.embed-calc-bottom {
  background: #2b5a99;
  margin: 0;
  color: #fff;
  padding: 10px;
  font-weight: bold;
}

.allcompanies-front {
  position: relative;
  font-size: 28px;
  color: #fff;
  line-height: 1.4;
  display: block;
  max-width: 640px;
  margin: 0 auto;
  text-align: center;
  margin: 0 auto 20px auto;
}

.allcompanies-front::before {
    content: '';
  position: absolute;
  height: 100%;
  transform: translateY(0) rotate(-0.3deg);
  width: 100%;
  background: #1b1b1b;
  left: -1%;
  z-index: -1;
  box-shadow: 0 0 10px rgba(0,0,0,0.4);
}




.disclosure {
  border-left: 2px solid #FFC40E;
}

.strong-rub {
  border-bottom: 1px solid #FFC40E;
}

.iframe-calc-front {
  padding: 0;
  margin: 0;
  iframe {
    max-width: 100%;
  }
}

@media all and (max-width: 520px) {
  .front-header__container {
    h3{
      font-size: 2.5rem;
    }
  }
}

@media all and (max-width: 640px) {
  .below-main-content {
    h2{
      font-size: 1.8rem;
    }
  }
  .front-header__container {
    width: 100%;
  }
  .newcomps__header__number {
    grid-template-columns: none;
    grid-template-rows: 1fr 1fr;
    max-height: 80px;
    h1 {
      font-size: 2.5rem;
    }
  }
  .below-main-content {
    padding: 20px 10px;
    ul {
      padding-left: 0;
    }
  }
}

@media all and (max-width: 1010px) {

  .iframe-calc-front {
    padding: 0 2rem 1rem;
  }

  .embed-calc-header {
    background: #2b5a99;
    color: #fff;
}

.iframe-calc-front {
  padding-top: 10px;
}

.newcompanies {
  font-size: 2rem;
}

.embed-calc-header {
  font-size: 2rem;
}

}



